import React, { Component } from "react";
import "./App.css";
import AppHeader from "./sections/header.js";
import Manifesto from "./sections/manifesto";
import Nosotros from "./sections/Nosotros.js";
import Icon from '@material-ui/core/Icon';
let lastScrollY = 0;
let ratio = 0;
let ratioBefore = -1;
let ratioBeforeBefore = -2;
let lastScrollTop = 0;
class App extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }
  nav = React.createRef();
  handleScroll = () => {
    lastScrollY = window.scrollY;
    let windowH = window.innerHeight;
    ratio = Math.floor(lastScrollY / windowH);
    ratioBefore = ratio - 1;
    document.getElementById("LogoContainer1").style.transform =
      "translateY(" + lastScrollY.toString() + "px)";
    document.getElementById("LogoContainer2").style.transform =
      "translateY(-" + lastScrollY.toString() + "px)";
      document.getElementById("Manifesto").style.transform =
      "translateY(calc(280vh - " + lastScrollY.toString()*1.5 + "px))";
    if(lastScrollY > windowH*1.3){
      document.getElementById("ManifestoText").classList.add('ManifestoVisible');
    }else{
      document.getElementById("ManifestoText").classList.remove('ManifestoVisible');
    }
    
    if (lastScrollY > windowH) {
      document.getElementById("circleWrapper").classList.add("circleVisible");
     

    } else {
     
      document
        .getElementById("circleWrapper")
        .classList.remove("circleVisible");
    }
    var e1 = document.getElementById("circle" + ratio.toString());
    var e2 = document.getElementById("circle" + ratioBefore.toString());
    var st = window.pageYOffset || document.documentElement.scrollTop; // Credits: "https://github.com/qeremy/so/blob/master/so.dom.js#L426"

    if (st > lastScrollTop) {
      if (e1 != null) {
        e1.classList.add("circleActive");
        if (e2 != null) {
          e2.classList.remove("circleActive");
        }
      }
    } else {
      ratio = Math.ceil(lastScrollY / windowH);
      ratioBefore = ratio - 1;
      if (ratio == 2) {
        document.getElementById("circle1").classList.add("circleActive");
        document.getElementById("circle2").classList.remove("circleActive");
      }
      if (e2 != null) {
        e1 = document.getElementById("circle" + ratio.toString());
        e2 = document.getElementById("circle" + ratioBefore.toString());
        e2.classList.add("circleActive");
        if (e1 != null) {
          e1.classList.remove("circleActive");
        }
      }
    }
    lastScrollTop = st <= 0 ? 0 : st;
  };
  render() {
    return (
      <div className="App">
        <nav ref={this.nav} />
        <AppHeader />
        <Manifesto />
        <Nosotros />
        <Manifesto />
        <Manifesto />
        <Manifesto />
        <Manifesto />
        <Manifesto />
        <Manifesto />
        <div id="circleWrapper" className="circleWrapper">
          <div id="circle1" className="circle" />
          <div id="circle2" className="circle" />
          <div id="circle3" className="circle" />
          <div id="circle4" className="circle" />
          <div id="circle5" className="circle" />
          <div id="circle6" className="circle" />
          <div id="circle7" className="circle" />
          <div id="circle8" className="circle" />
        </div>
       
      </div>
    );
  }
}

export default App;
