import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import '../App.css';
import Colors from '../color.js';

const styleManifesto ={
    HeaderContainer:{
        backgroundColor:'white',
        height: '100vh',
        width:'100vw',
    }
}

class Manifesto extends Component{
    render(){
        return(
            <div style={styleManifesto.HeaderContainer}>
               
                <div id="Manifesto" className="Manifesto">
                    <span id="ManifestoText">
                    Somos una agencia que no cree en los estereotipos.  Partimos desde la conceptualización y reformulamos los perfiles sociales a través del estudio constante, con el objetivo de descubrir lo que realmente tu marca y tus clientes necesitan. 
                    <br/> <br/> 
                    Quemamos los moldes porque cada marca es única.  Descubre qué es eso que te hace diferente y grítalo... que el mundo quiere escucharte.
                    </span>
                </div>
                <div className="Bienvenidos">¡Bienvenidos a Flama!</div>
            </div>
        )
    }
}

export default Manifesto