import React, { Component } from 'react';
import Logo from '../assets/logo.png'
import 'bootstrap/dist/css/bootstrap.css';
import Paper from '@material-ui/core/Paper';
import '../App.css';
import ReactDOM from 'react-dom';
import Grid from '@material-ui/core/Grid';
import Colors from '../color.js';
const styleHeader ={
    HeaderContainer:{
        backgroundColor:'white',
        height: '100vh',
        display:'flex',
        flexDirection:'row',
        width:'100%',
    },
    LogoContainer:{
        height: '100vh',
        backgroundColor:Colors.gray,
        width:'calc(50vw + 2px)',
        overflow:'hidden',
        zIndex:2,
        display:'flex',
        position:'fixed',
        justifyContent: 'center',
        alignItems: 'center',
    },
    Rombo:{
        minWidth:'80vw',
        marginLeft: '50vw',
        height:'80vw',
        maxHeight:'100vh',
        transform:'rotate(45deg)',
        transformOrigin:'center',
        backgroundColor:Colors.primaryDark,
    },
    LogoContainer2:{
        height: '100vh',
        backgroundColor:Colors.gray,
        overflow:'hidden',
        zIndex:2,
        width:'50vw',
        display:'flex',
        position:'fixed',
        marginLeft: '50vw',
        justifyContent: 'center',
        alignItems: 'center',
    },
    Rombo2:{
        minWidth:'80vw',
        marginLeft: '-50vw',
        maxHeight:'100vh',
        height:'80vw',
        transform:'rotate(45deg)',
        transformOrigin:'center',
        backgroundColor:Colors.primaryDark
    }
}

class AppHeader extends Component{
    render(){
        return (
         <div style={styleHeader.HeaderContainer} onScroll={this._handleScroll}>
            <div id="LogoContainer1" style={styleHeader.LogoContainer}  >
                <div className="Rombo" style={styleHeader.Rombo}/>
            </div>
            <div id="LogoContainer2" style={styleHeader.LogoContainer2}>
                <div className="Rombo" style={styleHeader.Rombo2}></div>
            </div>
         </div>
        )
    }
}
export default AppHeader;

