import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Colors from '../color.js';
import '../App.css';

const styleNosotros ={
    HeaderContainer:{
        backgroundColor:'white',
        height: '100vh',
        width:'100vw',
    },
    Title:{
        padding: '1em',
        fontSize:'3em',
        fontWeight: 'bold',
        color: Colors.naranjito,
    },
    Subtitle:{
        fontSize:'1.5em',
        fontStyle: 'italic',
        display:'block',
        fontWeight: 'bold',
    },
    text:{
        fontSize:'1em',
        display:'block'
    }
}

class Nosotros extends Component{
    render(){
        return(
            <div style={styleNosotros.HeaderContainer}>
               <h1 style={styleNosotros.Title}>Nosotros</h1>
               <div class="box" id="Busca">
               <h2 class="TextLeft" style={styleNosotros.Subtitle}>Flama busca...</h2>
               <span style={styleNosotros.text} class="TextLeft">
               Ser líder dentro del rubro de agencias publicitarias en desarrollar marcas 
               auténticas y empoderadas con valores que trascienden estereotipos.
               </span>
               </div>
                <div style={styleNosotros.Subtitle} class="box" id="Hacemos">
                <h2>Lo hacemos...</h2><br/>
               <span>
               A través del estudio constante y consciente a los requerimientos del consumidor de 
               cada cliente. Esto nos permite desarrollar soluciones con valor que suman a cada marca.
               </span>
                </div>
            </div>
            
        )
    }
}

export default Nosotros